    let BGC = (s.darkMode==0) ? 0xfbfaf2 : 0x04050D;
    let TC = (s.darkMode==0) ? "#444444" : "#EEEEEE";
    let TC2 = (s.darkMode==0) ? "#555555" : "#CCCCCC";
