    this.add( this.titleText2 );


    // this.add( this.scene.add.text(-325, -10,
    //   'Options/',
    //    { font: "26px Verdana", fill: "#555555" })
    //      .setOrigin(0, 0.5) );


    let dy1 = 0;
    let dy2 = 20;
    let dy3 = -80;

    this.add( this.scene.add.text(-300, 20+dy3,
      'Dark mode',
       { font: "26px Verdana", fill: BBT })
         .setOrigin(0, 0.5) );

    this.add( this.scene.add.circle(280,25+dy3,25,BB) );
    this.add( this.scene.add.circle(230,25+dy3,25,BB) );
    this.arc3 = this.scene.add.line(230,25+dy3,0,0,50,0,BB)
        .setOrigin(0,0);
    this.arc3.isStroked = true;
    this.arc3.setLineWidth(25,25);
    this.add(this.arc3);

    this.bgarc3 = this.scene.add.line(230,25+dy3,0,0,50,0,0xffffff)
        .setOrigin(0,0);
    this.bgarc3.isStroked = true;
    this.bgarc3.setLineWidth(20,20);
    this.add(this.bgarc3);
    this.c13 = this.scene.add.circle(230,25+dy3,20,0xffffff);
    this.add( this.c13 );
    this.c23 = this.scene.add.circle(280,25+dy3,20,0xff0000);
    this.add( this.c23 );


    this.add( this.scene.add.text(-300, 20+dy1,
      'Use alternative color scheme',
       { font: "26px Verdana", fill: BBT })
         .setOrigin(0, 0.5) );

    this.add( this.scene.add.circle(280,25+dy1,25,BB) );
    this.add( this.scene.add.circle(230,25+dy1,25,BB) );
    this.arc = this.scene.add.line(230,25+dy1,0,0,50,0,BB)
        .setOrigin(0,0);
    this.arc.isStroked = true;
    this.arc.setLineWidth(25,25);
    this.add(this.arc);

    this.bgarc = this.scene.add.line(230,25+dy1,0,0,50,0,0xffffff)
        .setOrigin(0,0);
    this.bgarc.isStroked = true;
    this.bgarc.setLineWidth(20,20);
    this.add(this.bgarc);
    this.c1 = this.scene.add.circle(230,25+dy1,20,0xffffff);
    this.add( this.c1 );
    this.c2 = this.scene.add.circle(280,25+dy1,20,0xff0000);
    this.add( this.c2 );

    this.add( this.scene.add.text(-300, 80+dy2,
      'Disable color overwriting',
       { font: "26px Verdana", fill: BBT })
         .setOrigin(0, 0.5) );

   this.add( this.scene.add.circle(280,85+dy2,25,BB) );
   this.add( this.scene.add.circle(230,85+dy2,25,BB) );
   this.arc2 = this.scene.add.line(230,85+dy2,0,0,50,0,BB)
       .setOrigin(0,0);
   this.arc2.isStroked = true;
   this.arc2.setLineWidth(25,25);
   this.add(this.arc2);

   this.bgarc2 = this.scene.add.line(230,85+dy2,0,0,50,0,0xffffff)
       .setOrigin(0,0);
   this.bgarc2.isStroked = true;
   this.bgarc2.setLineWidth(20,20);
   this.add(this.bgarc2);
   this.c12 = this.scene.add.circle(230,85+dy2,20,0xffffff);
   this.add( this.c12 );
   this.c22 = this.scene.add.circle(280,85+dy2,20,0xff0000);
   this.add( this.c22 );


    // this.add( this.scene.add.text(-325, -140,
    //   'About/',
    //    { font: "26px Verdana", fill: TC })
    //      .setOrigin(0, 0.5) );

     this.add( this.scene.add.text(-300, -130,
       'Made by Charles Matthews as a lockdown project.\nPowered by Phaser 3 + Javascript.',
        { font: "22px Verdana", fill: BBT })
          .setOrigin(0, 0.5) );


     // this.add( this.scene.add.text(-325, 145,
     //   'Feedback/',
     //    { font: "26px Verdana", fill: TC })
     //      .setOrigin(0, 0.5) );

    this.add(
      (new ColorButton(scene,180,130+85,80,300, 0x1DA1F2, 'Twitter     ', 'twitterImage') )
    );

    this.add(
    (new ColorButton(scene,-150,130+85,80,300, 0xd95821, 'E-mail     ', 'envelopeImage') )
    );

    this.add(
    (new ColorButton(scene,0,385,80,450, 0xaae1916, 'Delete all data       ', 'trashImage') )
    );

    this.confirmButton = (new ColorButton(scene,0,385,80,450, 0xaae1916, 'Tap to confirm       ', 'trashImage') );
    this.add( this.confirmButton );
    this.confirmButton.visible = false;


    this.xlabel = this.scene.add.text(320, -230, 'x', { font: "36px Arial", fill: BBT })
         .setOrigin(0.5, 0.5).setAlpha(0.7);

