  updateBars(n1,n2,n3,ttl1,ttl2,ttl3) {

    let MINWIDTH = 40;
    let MAXWIDTH = 300;

    let l1 = (n1<1) ? MINWIDTH : MAXWIDTH;
    let l2 = (n1<1) ? MINWIDTH : MINWIDTH + (MAXWIDTH-MINWIDTH)*(n2/n1);
    let l3 = (n1<1) ? MINWIDTH : MINWIDTH + (MAXWIDTH-MINWIDTH)*(n3/n1);

    this.bartext1.text = n1 + " ";
        this.bartext2.text = n2 + " ";
            this.bartext3.text = n3 + " ";

    this.bartext1.x = l1-200;
        this.bartext2.x = l2-200;
            this.bartext3.x = l3-200;

    this.bar1.width = l1;
        this.bar2.width = l2;
            this.bar3.width = l3;

    this.time1.text = (n1<1) ? '- - -' : this.parseTime(ttl1/n1);
        this.time2.text = (n2<1) ? '- - -' : this.parseTime(ttl2/n2);
            this.time3.text = (n3<1) ? '- - -' : this.parseTime(ttl3/n3);

  }

  addLeadingZero(s) {
    if (s>9)
      return s;

      return "0" + s;

  }

  parseTime(dt_) {

    let dt = Math.floor(dt_);

    if (dt<0)
      dt = 0;

    let hh = Math.floor(dt/3600);
    let mm = Math.floor(dt/60) % 60 ;
    let ss = Math.floor(dt) % 60 ;

    //if (hh>0)
      return this.addLeadingZero(hh) + ":" + this.addLeadingZero(mm) + ":" + this.addLeadingZero(ss);

    //return this.addLeadingZero(mm) + ":" + this.addLeadingZero(ss);

  }

