function implementDarkMode(s) {

    let WHITECOLOR = "#FFFFFF";
    let BLACKCOLOR = "#000000";
    let bb = 0x000000;
    let ww = 0xffffff;

  if (s.darkMode==1) {
    BLACKCOLOR = "#FFFFFF";
    WHITECOLOR = "#000000";
    bb = 0xffffff;
    ww = 0x000000;
  }

  document.body.style.background = WHITECOLOR;
  //global_this.cameras.main.setBackgroundColor(WHITECOLOR);

  global_this.titleText.setColor(BLACKCOLOR);
  global_this.titleLine1.setStrokeStyle(1,bb,1);
  global_this.titleLine2.setStrokeStyle(1,bb,1);

  global_this.menuButton.button.setTintFill(bb);
  global_this.questionButton.button.setTintFill(bb);
  global_this.trophyButton.button.setTintFill(bb);
  global_this.eyeButton.button.setTintFill(bb);

  timerLabel.label.setColor(BLACKCOLOR);

  gph.implementDarkMode(s);

  if (welcomeScreen.visible==false)
    welcomeScreen = new WelcomeScreen(global_this, width/2, height/2-75, height, termsURL, s );

  finishScreen = new FinishScreen(global_this,width/2,height/2, s, continueCallback);
  menuScreen = new MenuScreen(global_this,width/2,height/2-100, s, setPalette, deleteAll, setOverwrite,darkmodeCallback);
  helperScreen = new HelperScreen(global_this, width/2, height/2, s, turnOnHelperMode);

}

function resolveState(s) {

  console.log(s);

    if (s.dayID!=dayID) {
      s.dayID = dayID;
      s.time1 = 0;
      s.time2 = 0;
      s.time3 = 0;
      s.net_avg_time1=0;
      s.net_avg_time2=0;
      s.net_avg_time3=0;
      s.net_avg_comp1=0;
      s.net_avg_comp2=0;
      s.net_avg_comp3=0;
      s.helper1 = 0;
      s.helper2 = 0;
      s.helper3 = 0;
      s.completed = 0;
      s.puzID = 0;
      updateServer(s);
    }

    //s.puzID = 2;

    let puzData = data.Puzzles[s.puzID][dayID];
    let puzEndpoints = puzData.p;
    let puzConnections = data.Connections[ puzData.t ];
    let puzPositions = data.Positions[ puzData.t ];

    gph = new Graph(global_this, puzPositions, puzConnections, puzEndpoints , width, height, s );

    timerLabel.setStars(userState.puzID + userState.completed);

    implementDarkMode(s);
    turnOffHelperMode();

    if (welcomeScreen.visible){
      return;
    }

    if (s.completed) {

      timerLabel.stopTimer();
      if (s.puzID==0)
        timerLabel.writeTimerLabel(s.time1);

      if (s.puzID==1)
        timerLabel.writeTimerLabel(s.time2);

      if (s.puzID==2)
        timerLabel.writeTimerLabel(s.time3);

      finishScreen.show(s,false);
      gph.isCompleted = true;

    } else {
      timerLabel.startTimer();
    }

    gph.show();

}

