function turnOffHelperMode() {
  global_this.eyeButton.setAlpha(1);
  gph.helperMode = false;

  if (userState.completed==1)
    return;

    if (userState.puzID==0)
      userState.helper1 = 0;
    if (userState.puzID==1)
      userState.helper2 = 0;
    if (userState.puzID==2)
      userState.helper3 = 0;

}

function turnOnHelperMode() {

  global_this.eyeButton.setAlpha(0.2);

  if (gph.helperMode) {
    return;
  }

  console.log('Helper Mode Turned On');

  gph.helperMode = true;

  for (let ii=0; ii<gph.EE.length; ii++) {
    gph.lockColor( ii + 1 );
  }

  if (userState.puzID==0)
    userState.helper1 = 1;
  if (userState.puzID==1)
    userState.helper2 = 1;
  if (userState.puzID==2)
    userState.helper3 = 1;


}

function onMouseDown(p) {
  console.log('onMouseDown');

  if (welcomeScreen.visible==true) {
    return;
  }
  if (helperScreen.visible==true){
    helperScreen.mouseDown(p.x - width/2, p.y - height/2);
    return;
  }

  if (menuScreen.visible) {
    menuScreen.mouseDown(p.x - width/2, p.y - height/2);
    return;
  }
  if (finishScreen.visible) {
    return;
  }
  if (twoFingerTouch) {
    return;
  }



  gph.handleMouseDown(p);

  if (gph.nodeSelected>=0) {
    gph.mouseArc.updateStartpoint( gph.nodelist[ gph.nodeSelected ] );
    gph.mouseArc.show();
  }

  onMouseMove(p);
}

function onMouseMove(p) {
  if (p.isDown==false)
      return;
  if (twoFingerTouch) {
    return;
  }

  if (welcomeScreen.visible==true) {
    return;
  }
  if (helperScreen.visible==true) {
    return;
  }
  if (finishScreen.visible) {
    return;
  }
  if (menuScreen.visible) {
    return;
  }

  if (gph.nodeSelected<0)
    return;


  if (gph.checkMouseOver(p)>=0) {
    if (gph.nodeSelected>=0)
      gph.mouseArc.updateStartpoint( gph.nodelist[ gph.nodeSelected ] );
    else {
        gph.mouseArc.hide();
        gph.completed();
    }
  }

  gph.mouseArc.updateEndpoint( p.x, p.y );

  if (gph.isCompleted &&  timerLabel.timerStarted ) {
    completePuzzle();
  }

  //console.log('onMouseMove');

}

function completePuzzle() {

  timerLabel.stopTimer();
  timerLabel.setStars(userState.puzID+1);
  userState.completed = 1;

  let tt = Math.floor( timerLabel.deltaTime );

  if (userState.puzID==0) {
    userState.time1 = tt;
    userState.total_time1 += tt;
    userState.total_completed1 += 1;
  }

  if (userState.puzID==1) {
    userState.time2 = tt;
    userState.total_time2 += tt;
    userState.total_completed2 += 1;
  }

  if (userState.puzID==2) {
    userState.time3 = tt;
    userState.total_time3 += tt;
    userState.total_completed3 += 1;
  }

    console.log('Completed');
    updateServer( userState );

    gph.flash( showFinishScreen );
    saveFile();





}

function deleteAll() {

  if (currentlyDeleting)
    return;

  currentlyDeleting = true;
  deleteServer(userState, delCallback);

}

function delCallback() {

  console.log('Deleted');
  userState = {};
  saveFile();

  location.reload();

}

function onMouseUp(p) {
  if (welcomeScreen.visible) {
    welcomeScreen.hide();
    gph.show();
    timerLabel.startTimer();
    //gph.nodeSelected = -1;
    gph.selectNode( -1 );
    gph.mouseArc.hide();
    return;
  }
  if (helperScreen.visible) {
    gph.selectNode( -1 );
    gph.mouseArc.hide();
    return;
  }
  if (menuScreen.visible) {
    //gph.nodeSelected = -1;
    gph.selectNode( -1 );
    gph.mouseArc.hide();
    return;
  }
  if (finishScreen.visible) {
    //gph.nodeSelected = -1;
    gph.selectNode( -1 );
    gph.mouseArc.hide();
  }
  gph.handleMouseUp(p);
  gph.mouseArc.hide();

  finishScreen.mouseDown(p.x - width/2, p.y - height/2);

}

function getURL() {

  return 'https://5673wz6h6c.execute-api.eu-west-2.amazonaws.com/linkr';

}

function deleteServer(s, callback ) {

  let ss = {
    uuid : s.uuid,
    delme : 1,
  }


  axios.post(getURL(), ss)
    .then(res => {
      console.log(`statusCode: ${res.status}`);
      console.log(res);
      callback();

    })
    .catch(error => {
      console.error(error);
      callback();
    })


}

function updateServer(s) {

  let ss = {
    uuid : s.uuid,
    dayID : s.dayID,
    time1 : s.time1,
    time2 : s.time2,
    time3 : s.time3,
    total_time1 : s.total_time1,
    total_time2 : s.total_time2,
    total_time3 : s.total_time3,
    total_completed1 : s.total_completed1,
    total_completed2 : s.total_completed2,
    total_completed3 : s.total_completed3,
    helper1 : s.helper1,
    helper2 : s.helper2,
    helper3 : s.helper3,
    offset : s.offset,
  };

axios.post(getURL(), ss)
  .then(res => {
    console.log(`statusCode: ${res.status}`);
    console.log(res);

    if ("data" in res ) {
      var data_dayid = res["data"].dayid;
        console.log(res["data"], data_dayid);
      if (data_dayid == null) {
        return;
      }
      if (data_dayid!=userState.dayID) {
        return;
      }
      if (userState.net_avg_time1==0) {
        userState.net_avg_time1 = ( userState.time1<=0 ) ? 0 : res["data"].at1;
        userState.net_avg_comp1 = ( userState.time1<=0 ) ? 0 : res["data"].ac1;
      }
      if (userState.net_avg_time2==0) {
        userState.net_avg_time2 = ( userState.time2<=0 ) ? 0 : res["data"].at2;
        userState.net_avg_comp2 = ( userState.time2<=0 ) ? 0 : res["data"].ac2;
      }
      if (userState.net_avg_time3==0) {
        userState.net_avg_time3 = ( userState.time3<=0 ) ? 0 : res["data"].at3;
        userState.net_avg_comp3 = ( userState.time3<=0 ) ? 0 : res["data"].ac3;
      }

      console.log( userState );

      finishScreen.updateNetTimes( userState );
      saveFile();
    }

  })
  .catch(error => {
    console.error(error);
  })

}
