import 'phaser';


import {Node, Arc, MouseArc, BoldArc, CoronaNode, CoronaArc} from 'node.js';

const GAMESIZE = 360;
const TIMELENGTH = 350;

function rgb(r,g,b) {
  return Phaser.Display.Color.GetColor(255*r,255*g,255*b);
}

export var colorArray = [
  rgb(1,1,1),
  rgb(1, 0, 0), // red
  rgb(0, 0, 1), // blue
  rgb(0, 1, 0), // green
  rgb(0, 1, 1),// aqua
  rgb(0.54, 0.17, 0.89), // blue violet
  rgb(1, 0.8 , 0),// yellow
  rgb(0.1, 0.4, 0.1), // dark green
  rgb(1, 0.4, 0.7),// pink
  rgb(0.6, 0.32, 0.18), // sienna/brown
  rgb(1, 1, 0.75), //cream
  rgb(.82, .71, .55), // tan
  rgb(.44, 0.5, .56),// slate grey
  rgb(1, 0.46, 0.1), // burnt orange
  rgb(0.35 , 0, 0.0), // dark maroon
  rgb(1, 1, 0.5),
  rgb(.5, .5, 0.5)
]


export var alt_colorArray = [
  rgb(1,1,1),
  0xcc6677, // red
  0x332288, // blue
  0x117733, // green
  0x44aa99,// aqua
  0x999933, // dark green
  0xaa4499, // blue violet
  0xddcc77,// yellow
  0x882255,// pink
  0x88ccee, // sienna/brown
  0xaaaaaa, //cream
  0xee7733, // tan
  0xbbccee,
  0xffcccc,
  0x997700,// slate grey
  rgb(1, 1, 0.5),
  rgb(.5, .5, 0.5)
]


export class Graph extends Phaser.GameObjects.Container {

  constructor(scene, PP, CC, EE, width, height, s ) {

    super(scene);
    this.scene = scene;
    this.x = 0;
    this.y = 0;
    this.ww = width
    this.hh = height
    this.PP = PP;
    this.CC = CC;
    this.EE = [];
    this.HH = [];

    this.helperMode = false;


    this.vx = 0;
    this.vy = 0;
    this.nx = 0;
    this.ny = 0;
    this.vsf = 1;
    this.nsf = 1;

    this.emptyNodeColor = 0x000000;

    this.posChanged = false;

    this.portraitFlag = (height > width);
    this.paletteFlag = s.palette;
    this.overwriteFlag = s.preventOverwrite;
    this.darkModeFlag = s.darkMode;
    this.uState = s;

    this.nodeSelected = -1;

    this.isCompleted = false;

    this.nodelist = [];
    this.all_arcs = [];
    this.all_bold_arcs = [];
    this.arclist = {};

    this.corona_nodelist = [];
    this.corona_arclist = {};
    this.all_corona_arcs = [];

    this.is_endpoint = [];
    this.twin_endpoint = [];
    this.occupancy = [];
    this.arcOccupancy = {};
    this.colorLocked = {};

    this.mouseArc = new MouseArc( this.scene );

    for (let ii=0;ii<EE.length;ii++) {
      this.EE.push( EE[ii][0] );
      this.HH.push( EE[ii][1] );
    }


    let k=0;

    // NODES
    k=0;
    for (const n of PP ) {

      let x = (width>height) ? n[0] : n[1];
      let y = (width>height) ? n[1] : -n[0];

      let tmp = new Node(scene, width/2 + GAMESIZE * x,  height/2 + GAMESIZE * y, k);

      let tmp2 = new CoronaNode(scene, width/2 + GAMESIZE * x,  height/2 + GAMESIZE * y )

      this.nodelist.push( tmp );

      this.corona_nodelist.push( tmp2 );

      this.occupancy.push( 0 );
      this.is_endpoint.push( false );
      this.twin_endpoint.push( -1 );

      //this.add(tmp);

      k++;

    }

    k = 1;
    for (const e of this.EE) {

      let e1 = e[0];
      let e2 = e[1];

      this.nodelist[e1].setLabel(k);
      this.nodelist[e2].setLabel(k);

      this.nodelist[e1].setOuterColor( this.getPalette(k) );
      this.nodelist[e2].setOuterColor( this.getPalette(k) );

      this.occupancy[e1] = k;
      this.occupancy[e2] = k;
      this.is_endpoint[e1] = true;
      this.is_endpoint[e2] = true;
      this.twin_endpoint[e1] = e2;
      this.twin_endpoint[e2] = e1;

      this.colorLocked[k] = false;

      k++;

    }

    k = 0;
    for (const a of CC) {

      this.arclist[k] = {};
      this.corona_arclist[k] = {};
      this.arcOccupancy[k] = {};

      for (const aa of a) {
        if (aa<k) {
          continue;
        }

        let a1 = new Arc(scene, this.nodelist[k] , this.nodelist[aa]);
        let a2 = new BoldArc(scene, this.nodelist[k] , this.nodelist[aa]);
        let a3 = new CoronaArc(scene, this.nodelist[k] , this.nodelist[aa]);

        this.arclist[k][aa] = a2;
        this.corona_arclist[k][aa] = a3;
        this.arcOccupancy[k][aa] = 0;

        this.all_arcs.push(a1);
        this.all_bold_arcs.push(a2);
        this.all_corona_arcs.push(a3);

        this.add(a1);
      }

      k++;
    }

    k = 0;
    for (let ii=0;ii<this.corona_nodelist.length;ii++) {
      this.add( this.corona_nodelist[ii] );
    }

    k = 0;
    for (const a of CC) {
      for (const aa of a) {
        if (aa<k) {
          continue;
        }
      this.add( this.corona_arclist[k][aa] );
    }
    k++;
    }


    k = 0;
    for (const a of CC) {
      for (const aa of a) {
        if (aa<k) {
          continue;
        }
      this.add( this.arclist[k][aa] );
    }
    k++;
    }

    this.add(this.mouseArc);

    for (let ii=0;ii<this.nodelist.length;ii++) {
      this.add( this.nodelist[ii] );
    }


    this.selRing = new SelectionRing(this.scene,600,600);
    this.selRing.visible = false;
    this.add( this.selRing );

    this.scene.add.existing(this);
    this.alpha = 0;
    this.visible = false;
    this.animating = 0;
    this.start_anim = 0;
    this.rippleIndexes = [];

  };

  setPalette(p) {
    this.paletteFlag = p;
    this.refreshPalette();

  }

  implementDarkMode(s) {

    let ww = 0xd4cfbd;
    let bb = 0x000000;

    let hlc = 0x333333;

    if (s.darkMode==1) {
      ww = 0x000000;
      bb = 0xd4cfbd;
      hlc = 0xcccccc;
    }

    for (let ii=0;ii<this.all_arcs.length;ii++) {
      this.all_arcs[ii].setColor(bb);
      this.all_corona_arcs[ii].setColor(hlc);
    }

    for (let ii=0;ii<this.corona_nodelist.length;ii++) {
      this.corona_nodelist[ii].setColor(hlc);
    }


    this.emptyNodeColor = bb;
    this.refreshPalette();

  }

  setOverwrite(p) {
    this.overwriteFlag = p;

  }

  refreshPalette() {

    for (const n of this.nodelist) {
      this.updateNodeColor( n.nodenum );

      for (const c of this.CC[ n.nodenum]) {

        if (c<n.nodenum)
          continue;

          let v = this.get_arcOccupancy(c,n.nodenum);

          this.get_arclist(c,n.nodenum).setBoldColor( (v==0) ? -1 : this.getPalette(v) );

      }
    }



  }

  getPalette(k) {

    if (this.paletteFlag==1) {
      return alt_colorArray[k];
    }

    return colorArray[k];

  }

  selectNode( n ) {

    if (this.occupancy[n]>0) {
      if (this.colorLocked[ this.occupancy[n] ]) {
        return;
      }
    }

    let prevsel = this.nodeSelected;

    this.nodeSelected = n;

    if (n==-1) {
      this.selRing.visible = false;
      return;
    }


    this.selRing.visible = true;
    this.selRing.x = this.nodelist[n].x;
    this.selRing.y = this.nodelist[n].y;

    let cc = this.occupancy[n];

    if ((this.linked(this.EE[cc-1][0] , this.EE[cc-1][1])==false ) && (prevsel==-1)) {

      if (this.countConnections(n)==2) {
        this.cullMeUpTo(n);
      }

      this.beginRipple(n);

    }



  }

  beginRipple(n) {

    if (this.animating>0) {
      return;
    }


    this.start_anim = this.scene.time.now;
    this.animating = 5;
    this.rippleIndexes = [];

    let cc = this.occupancy[n];
    let st = this.EE[cc-1][0];
    if (this.linked(n, this.EE[cc-1][0])) {
      st = this.EE[cc-1][1];
    }

    this.rippleIndexes.push(st);

    let prev = st;
    let nn = st;
    let foundOne = true;


    while (foundOne) {
      foundOne = false;

        for (const c of this.CC[nn]) {
          if (c==prev) {
            continue;
          }
          if (this.get_arcOccupancy(nn,c)>0) {
            this.rippleIndexes.push(c);
            prev = nn;
            nn = c;
            foundOne = true;
            break;
          }
        }
    }

  }

  show() {
    if (this.visible==true)
      return;
    if (this.animating>0)
      return;

    this.visible = true;
    this.start_anim = this.scene.time.now;
    this.animating = 1;
    this.implementDarkMode(this.uState);

  }

  hide(callback) {
    if (this.visible==false)
      return;
    if (this.animating>0)
      return;

    this.visible = true;
    this.callback = callback;
    this.start_anim = this.scene.time.now;
    this.original_vsf = this.vsf;
    this.animating = 2;

  }

  updatePositions() {

    if (this.posChanged==false)
      return;
    this.posChanged = false;

    let xx = this.vx;
    let yy = this.vy;
    let sf = this.vsf;

    for (let ii=0; ii<this.nodelist.length; ii++) {

      this.nodelist[ii].x = this.ww/2 + (this.nodelist[ii].original_x - this.ww/2 )*sf + xx;
      this.nodelist[ii].y = this.hh/2 + (this.nodelist[ii].original_y - this.hh/2 )*sf + yy;

      this.corona_nodelist[ii].x = this.nodelist[ii].x;
      this.corona_nodelist[ii].y = this.nodelist[ii].y;
    }

    for (let ii=0; ii<this.all_arcs.length; ii++) {
      this.all_arcs[ii].updateArc();
      this.all_bold_arcs[ii].updateArc();
      this.all_corona_arcs[ii].updateArc();
    }

  }

  updateScreen() {

    if (this.visible==false)
      return;

    if (this.nsf>1.25) {
      this.nsf = 1.25;
    }
    if (this.nsf<0.75) {
      this.nsf = 0.75;
    }

    if (this.nx<-100){
      this.nx=-100;
    }
    if (this.nx>100) {
      this.nx=100;
    }

    if (this.ny<-100){
      this.ny=-100;
    }
    if (this.ny>100) {
      this.ny=100;
    }

    if (this.portraitFlag==false) {
      this.ny = 0;
    } else {
      this.nx = 0;
    }

    this.vy = this.ny;
    this.vx = this.nx;
    this.vsf = this.nsf;

    this.updatePositions();

    if (this.selRing.visible) {
      this.selRing.updateLines();
    }

    if (this.animating==0) {
      return;
    }

    let dt = (this.scene.time.now - this.start_anim)/TIMELENGTH;
    if (dt<0)
      dt = 0;

    if (this.animating==1) {
      dt-= 0.5;
      if (dt<0){
        this.alpha = 0;
        return;}
      if (dt>1) {
        this.animating=0;
        this.alpha = 1;
        this.nsf = 1;
        this.posChanged = true;
        this.updatePositions();
        return;
      }
      this.alpha = dt;
      this.nsf = 0.9 + 0.1*dt;
      this.posChanged = true;
      this.updatePositions();
      return;
    }

    if (this.animating==2) {
      if (dt>1) {
        this.animating=0;
        this.alpha = 0;
        this.visible = false;
        this.callback();
        return;
      }
      this.alpha = 1-dt;
      this.nsf = this.original_vsf + 0.1*dt;
      this.posChanged = true;
      this.updatePositions();
      return;
    }

    if (this.animating==3) {
      if (dt>0.6) {
        this.animating=0;
        this.alpha = 1;
        this.visible = true;
        this.callback();
        return;
      }
      this.alpha = 0.3+0.7*(dt/0.6);
      return;
    }

    if (this.animating==4) {
      if (dt>0.6) {
        this.animating=0;
        this.starburst.destroy();
        this.setRipple(1);
        return;
      }
      this.starburst.updateLines(dt/0.6);
      return;
    }

    if (this.animating==5) {
      if (dt>0.5) {
        this.animating=0;
        this.setRipple(1);
        return;
      }
      this.setRipple(1.25-0.25*dt/0.5);
      return;
    }


  }

  setRipple(sf) {

    let prev=-1;

    for (const n of this.rippleIndexes) {
      this.nodelist[n].setRadius(sf);

      if (prev>-1){
        //console.log(n,prev, this.get_arcOccupancy(n,prev) );
        this.get_arclist(n,prev).setWidth(sf);
      }
      prev = n;

    }

  }

  completed() {

    if (this.isCompleted)
      return;

    for (const ep of this.EE) {

      if (this.linked(ep[0],ep[1])) {
        continue;
      } else {
        this.isCompleted = false;
        return false;
      }

    }

    this.isCompleted = true;
    return true;

  }

  linked(n1,n2) {

    let n = n1;
    let prev = n1;

    while (n!=n2) {

      let foundit = false;

      for (const nn of this.CC[n]) {

        if (nn==prev) {
          continue;
        }

        if (this.get_arcOccupancy(n,nn) ) {
          prev = n;
          n=nn;
          foundit = true;
          break;
        }

      }

      if (foundit==false) {
        return false;
      }

    }

    return true;

  }


  flash(callback) {
      if (this.animating==4) {
        this.animating=0;
        this.starburst.destroy();
      }
      if (this.animating==5) {
        this.setRipple(1);
        this.animating = 0;
      }


      this.visible = true;
      this.start_anim = this.scene.time.now;
      this.animating = 3;
      this.callback = callback;

  }

  handleMouseDown(p) {


  if (this.visible==false)
    return;

    console.log('Handing mousedown');
    this.selectNode(-1); // nodeSelected = -1;

    if (this.isCompleted)
      return;

    let foundOne = false;
    let closestR = 10000000;
    let closestN = 0;

    for (const n of this.nodelist) {
      var rr = Math.sqrt((p.x-n.x)*(p.x-n.x) + (p.y-n.y)*(p.y-n.y) );
      if (rr<closestR) {
        closestR=rr;
        closestN = n.nodenum;
      }
      if (this.occupancy[ n.nodenum ]==0) {
        continue;
      }
      if (n.checkInside(p.x,p.y)) {
        //this.nodeSelected = n.nodenum;
        this.selectNode( n.nodenum );
        foundOne = true;
        break;
      }
    }

    if (foundOne==false) {
      if ((this.occupancy[ closestN ]!=0) && (closestR<200) ) {
        if (this.nodelist[closestN].checkInside(  this.nodelist[closestN].x, this.nodelist[closestN].y)) {
          //this.nodeSelected = closestN;
          this.selectNode( closestN );
        }
      }
    }

  }

  lockColor(cc) {

    if (this.helperMode==false){
      return;
    }

    let ep = this.EE[ cc - 1 ];

    if (this.linked(ep[0],ep[1])==false) {
      return;
    }


    let h = this.HH[cc-1];

    for (let ii=0; ii<this.occupancy.length; ii++) {

      let aa = h.includes(ii);
      let bb = (this.occupancy[ii] ==cc);

      if (aa && bb)
        continue;

      if ( (!aa) && (!bb) ) {
        continue;
      }

      return;


    }

    this.colorLocked[ cc ] = true;

    for (let ii=0; ii<this.corona_nodelist.length;ii++) {
      if (this.occupancy[ii]==cc) {
        this.corona_nodelist[ii].show();
        if (this.is_endpoint[ii]==0){
          this.nodelist[ii].setLabel("•");
          this.nodelist[ii].label.setAlpha(0.5);
        }
      }
    }

    let k = 0;
    for (const a of this.CC) {
      for (const aa of a) {
        if (aa<k) {
          continue;
        }
      if (this.get_arcOccupancy(k,aa)==cc) {
        this.corona_arclist[k][aa].show();
      }
    }
    k++;
    }


  }

  checkMouseOver(p) {
    if (this.nodeSelected<0)
      return -1;

    let insideAny = false;

    for (const n of this.nodelist) {

      if (n.checkInside(p.x,p.y)) {

        insideAny = true;

        if (n.nodenum==this.nodeSelected)
          continue;

        if (this.overwriteFlag) {
          // Ignore if color is not zero or same
          if ( (this.occupancy[n.nodenum]!=0) && (this.occupancy[n.nodenum]!=this.occupancy[this.nodeSelected]) ){
            continue;
          }
        }

        let cc = 0;
        let ep = 0;
        if (this.trialExtend(n) ) {
          cc = this.occupancy[n.nodenum];
          console.log(cc);
          ep = this.findEndpoint( this.EE[cc-1][0] , this.EE[cc-1][0]  )
          {
            if (ep==this.twin_endpoint[ this.EE[cc-1][0] ]) {
              // Color completed!
              if (this.animating==5) {
                this.setRipple(1);
                this.animating = 0;
              }
              if (this.animating==0) {
                this.animating=4;
                this.start_anim = this.scene.time.now;
                this.starburst = new Starburst(this.scene,n.x,n.y, this.getPalette(cc) );
              }
              //this.nodeSelected = -1;
              this.selectNode( -1 );
              this.lockColor(cc);
            }
          }

          return n.nodenum;
        }

        if ((this.occupancy[ n.nodenum]==0) || ( (this.occupancy[ n.nodenum]==this.occupancy[this.nodeSelected])
                &&
              (this.is_endpoint[n.nodenum]) ) ) {
          // Find cxn closer
          let dx = (n.x - this.nodelist[this.nodeSelected].x);
          let dy = (n.y - this.nodelist[this.nodeSelected].y);
          let dd = dx*dx + dy*dy;
          let mind = this.nodeSelected;

          for (const c of this.CC[this.nodeSelected]) {
            if (this.occupancy[c]!=0)
              continue;
            dx = (n.x - this.nodelist[c].x);
            dy = (n.y - this.nodelist[c].y);
            if ((dx*dx+dy*dy)<dd) {
              dd = (dx*dx+dy*dy);
              mind = c;
            }
          }
          console.log(this.nodeSelected, mind, dd);

          if (mind!=this.nodeSelected) {
            if (this.trialExtend( this.nodelist[mind] ) ) {
              return n.nodenum;
            }
          }


        }

      }
    }

    if (insideAny==true)
      return -1;

    let dx = ( p.x - this.nodelist[this.nodeSelected].x);
    let dy = ( p.y - this.nodelist[this.nodeSelected].y);

    let myd = Math.sqrt( dx*dx + dy*dy );

    let allcloserflag = true;

    let new_node = -1;
    let new_dist = myd;

    for (const c of this.CC[this.nodeSelected]) {

      let ddx = ( this.nodelist[c].x - this.nodelist[this.nodeSelected].x);
      let ddy = ( this.nodelist[c].y - this.nodelist[this.nodeSelected].y);
      let mydd = Math.sqrt( ddx*ddx + ddy*ddy );

      if (mydd*1.5>myd) {
        allcloserflag = false;
      }

      if (this.occupancy[c]==0) {
        ddx = ( p.x - this.nodelist[c].x);
        ddy = ( p.y - this.nodelist[c].y);
        if (Math.sqrt(ddx*ddx+ddy*ddy)<new_dist) {
          new_dist = Math.sqrt(ddx*ddx+ddy*ddy);
          new_node = c;
          //console.log(c);
        }


      }

    }

    if (allcloserflag==false)
      return -1;

    // Pointer is further than all connected nodes
    // Choose empty node that puts us closer
    if (new_node>-1) {
      if (this.trialExtend(this.nodelist[new_node]) ) {
        console.log('moo',new_node);
        return new_node;
      }
    }

  }

  get_arclist(n1,n2) {
  let min_n = Math.min(n1 , n2);
  let max_n = Math.max(n1 , n2);
  return this.arclist[min_n][max_n];
  }
  get_arcOccupancy(n1,n2) {
  let min_n = Math.min(n1 , n2);
  let max_n = Math.max(n1 , n2);
  return this.arcOccupancy[min_n][max_n];
  }
  set_arcOccupancy(n1,n2,v) {
  let min_n = Math.min(n1 , n2);
  let max_n = Math.max(n1 , n2);
  this.arcOccupancy[min_n][max_n] = v;
  this.get_arclist(n1,n2).setBoldColor( (v==0) ? -1 : this.getPalette(v) );

  this.updateNodeColor(n2);
  this.updateNodeColor(n1);

  }
  set_occupancy(n,v) {
    this.occupancy[n] = v;
    this.updateNodeColor(n);
    this.completed();
  }

  updateNodeColor(n) {

    let v = this.occupancy[n];
    let cc = this.getPalette(v);

    if (v==0) {
      this.nodelist[n].setColor( this.emptyNodeColor );
    } else {
      // Count how many cxns
      let cxns = this.countConnections(n);

      if (cxns==0) {
        this.nodelist[n].setOuterColor(cc);
      }
      if (cxns==2) {
        this.nodelist[n].setColor(cc);
      }
      if (cxns==1) {
        if (this.is_endpoint[n]) {
          this.nodelist[n].setColor(cc);
        } else {
          this.nodelist[n].setOuterColor(cc);
        }
      }
    }

  }

  countConnections(n) {
    let cxns = 0;

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(c,n)>0)
        cxns++;
    }
    return cxns;
  }

  trialExtend(n) {

    if (this.areConnected(n.nodenum , this.nodeSelected)==false) {
      return false;
    }
    if ( this.occupancy[n.nodenum]>0) {
      if (this.colorLocked[ this.occupancy[n.nodenum] ]) {
        return false;
      }
    }

    let curnum = this.occupancy[ this.nodeSelected ];
    let curcol = this.getPalette( curnum );

    // Empty node
    if (this.occupancy[n.nodenum]==0) {

      this.cullMeUpTo(this.nodeSelected);

      this.set_occupancy(n.nodenum,curnum);

      this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);

      //this.nodeSelected = n.nodenum;
      this.selectNode( n.nodenum );

      return true;
    }

    // Same color
    if (this.occupancy[n.nodenum]==curnum) {

      if (this.get_arcOccupancy(this.nodeSelected, n.nodenum )>0 ) {

        if ( (this.countConnections(this.nodeSelected)==1) &&
            (this.is_endpoint[this.nodeSelected]==false) ) {
              this.set_occupancy(this.nodeSelected , 0);
              this.set_arcOccupancy(this.nodeSelected, n.nodenum,0);
              //this.nodeSelected = n.nodenum;
              this.selectNode( n.nodenum );
          return true;
        }

        if (this.is_endpoint[this.nodeSelected]==true) {

          if (this.linked( this.nodeSelected , this.twin_endpoint[this.nodeSelected] ) ) {

            this.set_arcOccupancy(this.nodeSelected, n.nodenum,0);
            this.selectNode( n.nodenum );
            return true;

          }

      }


        return false;
      }

      this.cullMeUpTo(this.nodeSelected);
      if (this.occupancy[n.nodenum]>0)
        this.cullMeUpTo(n.nodenum);

      if ((this.occupancy[n.nodenum]==curnum) && (this.occupancy[this.nodeSelected]==curnum)) {
        this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);
        //this.nodeSelected = n.nodenum;
        this.selectNode( n.nodenum );
      } else {

        if (this.occupancy[n.nodenum]==curnum) {
          //this.nodeSelected = n.nodenum;
          this.selectNode( n.nodenum );
          return true;
        }

        if (this.occupancy[this.nodeSelected]==curnum) {

          this.set_occupancy(n.nodenum, curnum);
          this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);
          //this.nodeSelected = n.nodenum;
          this.selectNode( n.nodenum );
          return true;
        }

      }

      return true;
    }

    // Different color
    // no if statement needed...

    if (this.is_endpoint[n.nodenum])
      return false;

    this.cullMeUpTo(this.nodeSelected);

    this.splitSnake(n.nodenum);

    this.set_occupancy(n.nodenum,curnum);

    this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);

    //this.nodeSelected = n.nodenum;
    this.selectNode( n.nodenum );

    return true;
  }

  splitSnake(n) {

    this.cullMeUpTo(n);

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(n,c)>0) {
        this.set_arcOccupancy(n,c,0);
      }
    }
  }

  cullMeUpTo(n) {

    let feps = [];

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(n,c)>0) {
        feps.push( this.findEndpoint(n,c) );
      }
    }

    console.log(feps);

    if (feps.length==0)
      return;

    if (feps.length==1) {
      if (this.is_endpoint[n]==false)
        return;
      if (this.is_endpoint[feps[0]]) {
        this.delRandomLink(n);
        return;
      }
      this.delSnake(feps[0],n);
      return;
    }

    let n1 = feps[0];
    let n2 = feps[1];

    let ep1 = this.is_endpoint[n1];
    let ep2 = this.is_endpoint[n2];

    if (ep1 && !ep2) {
        // delete from n2 to n
        this.delSnake(n2,n);
      return;
    }
    if (!ep1 && ep2) {
        // delete from n1 to n
        this.delSnake(n1,n);
      return;
    }
    if (ep1 && ep2) {
        this.delRandomLink(n);
      return;
    }

  }

  delRandomLink(n) {


  for (const c of this.CC[n]) {
    if (this.get_arcOccupancy(n,c)<=0)
      continue;
      this.set_arcOccupancy(n,c,0);

   break;
  }



  }

  delSnake(st,en) {

    let n = st;
    let bflag = false;

    while (n!=en) {
      this.set_occupancy(n,0);
      bflag = false;
      for (const c of this.CC[n]) {
        if (this.get_arcOccupancy(n,c)<=0)
          continue;

        this.set_arcOccupancy(n,c,0);
        n=c;
        bflag = true;
        break;
      }
      if (bflag==false) {
        console.log('Error');
        break;
      }
    }

  }

  findEndpoint(n1,n2) {

    for (const c of this.CC[n2]) {
      if (c==n1) {
        continue;
      }
      if (this.get_arcOccupancy(n2,c)<=0)
        continue;

        return this.findEndpoint(n2,c);
    }

    return n2;

  }

  areConnected(n1,n2) {

    if (this.CC[n2].includes(n1) ) {
      return true;
    }
    return false;

  }

  handleMouseUp(p) {

    if (this.nodeSelected<0)
      return;
    if (this.visible==false)
      return;

    if (this.is_endpoint[this.nodeSelected]) {

      if (this.nodelist[this.nodeSelected].checkInside(p.x,p.y)) {
        // Clear endpoint onwards
        console.log('clear snake');
        this.clearSnake( this.nodeSelected );

      }

    }

    //this.nodeSelected = -1;
    this.selectNode( -1 );

  }

  clearSnake(n) {

    if (this.is_endpoint[n]==false) {
      this.set_occupancy(n,0);
    }

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(n,c)>0) {

        this.set_arcOccupancy(n,c,0);
        this.clearSnake(c);

      }

    }


  }

}




export class Starburst extends Phaser.GameObjects.Container {

  constructor(scene,x,y , cc) {

    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.cc = cc;
    this.W = 5;

    this.arclist = []

    for (let ii=0;ii<8;ii++) {

      let aa = this.scene.add.line(0,0,0,0,0,0,cc)
          .setOrigin(0,0);
      aa.isStroked = true;
      aa.setLineWidth(this.W,this.W);
      this.add(aa);
      this.arclist.push(aa);
    }

    this.scene.add.existing(this);

  }

  updateLines(dt) {

    if (dt<0)
      dt = 0;
    if (dt>1)
      dt = 1;

    let w1 = 35 + dt*75;
    let w2 = 75 + dt*35;

    for (let ii=0;ii<8;ii++) {

      let ang = ii*3.1415/4;
      let xx = Math.sin(ang);
      let yy = Math.cos(ang);

      this.arclist[ii].setTo(w1*xx,w1*yy,w2*xx,w2*yy);

    }


  }

}




export class SelectionRing extends Phaser.GameObjects.Container {

  constructor(scene,x,y) {

    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.W = 5;

    this.arclist1 = []
    this.arclist2 = []

    for (let ii=0;ii<8;ii++) {

      let aa = this.scene.add.line(0,0,0,0,0,0,0x000000)
          .setOrigin(0,0);
      aa.isStroked = true;
      aa.setLineWidth(3,3);
      aa.setAlpha(0.75);
      this.add(aa);
      this.arclist1.push(aa);


      let bb = this.scene.add.line(0,0,0,0,0,0,0xffffff)
          .setOrigin(0,0);
      bb.isStroked = true;
      bb.setLineWidth(1,1);
      bb.setAlpha(0.75);
      this.add(bb);
      this.arclist2.push(bb);

    }

    this.scene.add.existing(this);

  }

  updateLines() {

    let dt = this.scene.time.now/1000;
    dt = dt % 1;

    let w1 = 30;
    let w2 = 40;

    for (let ii=0;ii<8;ii++) {

      let ang = (dt+ii)*3.1415/4;
      let xx = Math.sin(ang);
      let yy = Math.cos(ang);

      this.arclist1[ii].setTo(w1*xx,w1*yy,w2*xx,w2*yy);
      this.arclist2[ii].setTo(w1*xx,w1*yy,w2*xx,w2*yy);

    }


  }

}
