import 'phaser';

const NODERADIUS = 30;
const ARCRADIUS = 5;
const CORONAWIDTH = 1.25;
const CORONACOLOR = 0x444444;

export class Node extends Phaser.GameObjects.Container {

  constructor(scene, x, y, nodenum) {

    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.original_x = x;
    this.original_y = y;

    this.nodenum = nodenum;
    this.nodeColor = 0x000000;
    this.depth = 10;

    this.outerCircle = this.scene.add.circle(0,0,NODERADIUS,0x000000);
    this.innerCircle = this.scene.add.circle(0,0,NODERADIUS/1.4,0x000000);
    this.label = this.scene.add.text(0, 0, ' ', { font: "36px Arial", fill: "#fff" })
            .setOrigin(0.5, 0.5).setStroke(0x000000,10);

    this.add(this.outerCircle);
    this.add(this.innerCircle);
    this.add(this.label);

    this.scene.add.existing(this);
  };

  setRadius(sf) {

    this.outerCircle.setRadius(NODERADIUS*sf);

  }

  setLabel(label) {

    this.label.setText(label);

  }

  setColor(c) {
    this.innerCircle.setFillStyle(c,1);
    this.outerCircle.setFillStyle(c,1);
    this.nodeColor = c;

  }
  setOuterColor(c) {
    this.outerCircle.setFillStyle(c,1);
    this.innerCircle.setFillStyle(0x000000,1);
    this.nodeColor = c;
  }

  checkInside(x,y) {

    let dx = x - this.x;
    let dy = y - this.y;

    if (dx*dx < NODERADIUS*NODERADIUS*1.4) {
      if (dy*dy < NODERADIUS*NODERADIUS*1.4) {
        return true;
      }
    }

    return false;

  }

};


export class CoronaNode extends Phaser.GameObjects.Container {

  constructor(scene, x, y, nodenum) {

    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.original_x = x;
    this.original_y = y;

    this.nodenum = nodenum;
    this.nodeColor = 0x000000;
    this.depth = 10;

    this.outerCircle = this.scene.add.circle(0,0,NODERADIUS*CORONAWIDTH,CORONACOLOR);

    this.add(this.outerCircle);

    this.scene.add.existing(this);

    this.visible = false;
  };

  setColor(cc) {

    this.outerCircle.setFillStyle(cc,1);

  }

  show() {
    this.visible = true;

  }

};


export class Arc extends Phaser.GameObjects.Container {

  constructor(scene, n1, n2 ) {

    super(scene);
    this.scene = scene;
    this.x = n1.x;
    this.y = n1.y;
    this.n1 = n1;
    this.n2 = n2;

    this.setDepth(-100);

    let dx = n2.x - n1.x;
    let dy = n2.y - n1.y;

    this.arc = this.scene.add.line(0,0,0,0,dx,dy,0x000000)
        .setOrigin(0,0);
    this.arc.isStroked = true;
    this.arc.setLineWidth(ARCRADIUS,ARCRADIUS);


    this.add(this.arc);

    this.scene.add.existing(this);
  };

  updateArc() {

          let dx = this.n2.x - this.n1.x;
          let dy = this.n2.y - this.n1.y;
          this.x = this.n1.x;
          this.y = this.n1.y;
          this.arc.setTo(0,0,dx,dy);

    }

  setColor(c) {

    this.arc.setStrokeStyle(ARCRADIUS,c,1);
  }

};

export class BoldArc extends Phaser.GameObjects.Container {

  constructor(scene, n1, n2 ) {

    super(scene);
    this.scene = scene;
    this.x = n1.x;
    this.y = n1.y;
    this.c = 0x000000;

    this.n1 = n1
    this.n2 = n2

    this.setDepth(-50);

    let dx = n2.x - n1.x;
    let dy = n2.y - n1.y;

    this.boldarc = this.scene.add.line(0,0,0,0,dx,dy,0x000000)
        .setOrigin(0,0);

    this.boldarc.isStroked = true;
    this.boldarc.setLineWidth(NODERADIUS,NODERADIUS);
    this.boldarc.setAlpha(0);

    this.visible = false;

    this.add(this.boldarc);

    this.scene.add.existing(this);
  };

  updateArc() {

        let dx = this.n2.x - this.n1.x;
        let dy = this.n2.y - this.n1.y;
        this.x = this.n1.x;
        this.y = this.n1.y;
        this.boldarc.setTo(0,0,dx,dy);

  }

  setBoldColor(c) {
    if (c==-1) {
      this.boldarc.setAlpha(0);
      this.visible = false;
      return;
    }
    this.visible = true;
    this.boldarc.setStrokeStyle( NODERADIUS , c , 1 );
    this.boldarc.setAlpha(1);
    this.c = c;
  }

  setWidth(sf) {
    //this.boldarc.setStrokeStyle( NODERADIUS*sf , this.c , 1 );
    this.boldarc.setLineWidth(NODERADIUS*sf,NODERADIUS*sf);
  }

};

export class CoronaArc extends Phaser.GameObjects.Container {

  constructor(scene, n1, n2 ) {

    super(scene);
    this.scene = scene;
    this.x = n1.x;
    this.y = n1.y;

    this.n1 = n1
    this.n2 = n2

    this.setDepth(-50);

    let dx = n2.x - n1.x;
    let dy = n2.y - n1.y;

    this.boldarc = this.scene.add.line(0,0,0,0,dx,dy,CORONACOLOR)
        .setOrigin(0,0);

    this.boldarc.isStroked = true;
    this.boldarc.setLineWidth(NODERADIUS*CORONAWIDTH,NODERADIUS*CORONAWIDTH);

    this.visible = false;

    this.add(this.boldarc);

    this.scene.add.existing(this);
  };

  setColor(cc) {

    this.boldarc.setStrokeStyle(CORONAWIDTH * NODERADIUS , cc , 1 );
  }

  updateArc() {

        let dx = this.n2.x - this.n1.x;
        let dy = this.n2.y - this.n1.y;
        this.x = this.n1.x;
        this.y = this.n1.y;
        this.boldarc.setTo(0,0,dx,dy);

  }

  show() {
    this.visible = true;
  }

};



export class MouseArc extends Phaser.GameObjects.Container {

  constructor(scene ) {

    super(scene);
    this.scene = scene;
    this.x = 0;//n.x;
    this.y = 0;//n.y;
    this.depth = -10;

    this.arc = this.scene.add.line(0,0,0,0,0,0,0x000000)
        .setOrigin(0,0);
    this.arc.isStroked = true;
    this.arc.setLineWidth(NODERADIUS,NODERADIUS);

    this.circle = this.scene.add.circle(0,0,NODERADIUS,0x000000);

    this.arc.alpha = 0;
    this.circle.alpha = 0;

    this.add(this.arc);
    this.add(this.circle);

    this.scene.add.existing(this);
  };

  hide() {

    this.arc.alpha = 0;
    this.circle.alpha = 0;

  };

  show () {


        this.arc.alpha = 1;
        this.circle.alpha = 1;

  };


  updateEndpoint(x,y) {

    let dx = x - this.x;
    let dy = y - this.y;

    this.arc.setTo(0,0,dx,dy);

    this.circle.setPosition(dx,dy);

  };

  updateStartpoint(n) {


    this.x = n.x;
    this.y = n.y;
    let color = n.nodeColor;

    this.arc.setStrokeStyle(1,color,1);
    this.circle.setFillStyle(color,1);

    this.updateEndpoint(n.x,n.y);

    this.show();

  };

};
